<template>
<!--  组织机构使用样本-->
  <div >
    <el-card shadow="always"  >
      <el-container style="width:100%; border: 1px solid #eee">

        <el-aside   v-if="showTree" width="200px" style=" height: calc(100vh); background-color: rgb(238, 241, 246)">
          <el-tree
            style=" height: calc(100vh);"

            v-if="showTree"
            :data="treeData"

            default-expand-all
            node-key="id"
            ref="org_tree"
            highlight-current
            :props=" {children: 'detail',label: 'caption' }"
            @node-click="handleTreeNodeClick"

          >
            <span slot-scope="{ node, data }">
                <span style="font-size: 14px;">{{ node.label }}</span>
                <span  v-if="node.level===5">
                  <el-button type="text" size="mini" @click="() => showOrgInfo(data)">选择</el-button>
                </span>
            </span>

          </el-tree>

        </el-aside>
        <el-container style="width:100%; border: 1px solid #eee">
          <el-aside    width="25px" style=" height: calc(100vh); background-color: rgb(238, 241, 246)">

          <i :class="icon1"
             style="height:100%;display: flex;width:25px;
             flex-direction: row;
             justify-content: center;align-items: center;"
             @click="showHiddenTree"

          ></i>
          </el-aside>
          <el-main style="height: 100%;padding-top:0px;">

            <example4   ></example4>

          </el-main>
        </el-container>

      </el-container>
    </el-card>



    <el-dialog :visible.sync="dialogFormVisible1"
               :close-on-press-escape="false"
               :close-on-click-modal="false"
               :show-close="false"

    >
      <div slot="title"  style="text-align: left;font-size:20px;font-weight: bold;">
        <span  >共[{{this.communityRows.length}}]个小区</span>
      </div>

      <el-tabs  v-model="activeName"   type="border-card" tab-position="left" style="height: 400px;" @tab-click="trigerChildObj">

          <el-tab-pane
            style="width:100%;"
            v-for="(item, index) in editableTabs"

            :key="item.name"
            :label="item.title"
            :name="item.name">
                <el-tooltip class="item-tabs" effect="dark" :content="item.title" placement="left"  slot="label">
                  <span>{{item.title}}</span>
                </el-tooltip>

            <component :is="item.content"
                       :ref="item.name"
                       v-if="item.show"
                       :pri_detail="{
                          communityObj:{id:item.name,title:item.title},
                       }"
                       @parent_method="dialogFormVisible1=false"
            ></component>
          </el-tab-pane>



      </el-tabs>


      <div slot="footer" class="dialog-footer">

<!--        <el-button @click="">测 试</el-button>-->
        <el-button @click="dialogFormVisible1 = false">关 闭</el-button>

      </div>

    </el-dialog>



  </div>

</template>

<script>

import Example4 from "./Example4";
import Bus from "../../../utils/Bus";
export default {
  name: "Example3",
  components: {Example4},
  data() {
    return {
      dialogFormVisible1:false,

      activeName:'1',
      editableTabs:[],
      communityRows:[],

      icon1: "el-icon-caret-left",//"el-icon-d-arrow-left",
      showTree: true,
      currentSelTreeNodeId:'',//当前选择节点的ID
      treeData:[
        {id:'0',caption:'智慧供热平台',type:'0',detail:[
            {id:'100',caption:'热力公司1',type:'1',detail:[
                {id:'100_1',caption:'热力分公司1_1',type:'2',detail:[
                    {id:'100_1_1',caption:'热力所1',type:'3',detail:[
                        {id:'SI_1',caption:'换热站1',type:'4'},
                        {id:'SI_6',caption:'换热站2',type:'4'},
                      ]},
                    {id:'100_1_2',caption:'热力所2',type:'3'},
                  ]},
                {id:'100_2',caption:'热力分公司1_2',type:'2'},
              ]},
            {id:'200',caption:'热力公司2',type:'1',detail:[
                {id:'200_1',caption:'热力分公司2_1',type:'2'},
                {id:'200_2',caption:'热力分公司2_2',type:'2'},
              ]},
          ]}


        ]
    }
  },
  destroyed(){
    // console.info("进入Example3的destroyed方法");

    Bus.$off('globalMsg2');
  },
  created() {

  },
  mounted(){
    var that = this;
    Bus.$on('globalMsg2',function(val){//监听各组件传递的 globalMsg 事件
      console.log("监听到的消息："+val);

      console.info("curid:"+val.curid);
      console.info("method:"+val.method);
      console.info("layer:"+val.layer);
      console.info("target:"+val.target);
      console.info("caption:"+val.target.caption);
      if(val.method==='1'){//增加
        that.addTreeNode(val.curid, val.target ,val.layer);
      }else if(val.method==='2') {//修改
        that.modifyTreeNode(val.curid,val.target);
      }else if(val.method==='3') {//删除
        that.delTreeNode(val.curid);
      }

    });
  },
  methods:{
    queryCommunity(data){
      var that = this;

      this.editableTabs = [];

      console.info("当前ID："+data.id);
      let _jsondata_ =   {
        ci_station_id:data.id,
      } ;
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryCommunityKV,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          this.communityRows = res.data.rows;
          if(this.communityRows.length>0){
            this.communityRows[0].status = true;
            this.activeName = this.communityRows[0].ci_uuid;
            Bus.$emit('globalMsgForCommunityBuilder',{
              community:{ci_uuid: this.communityRows[0].ci_uuid,ci_community_name: this.communityRows[0].ci_community_name},
              builder:{bi_build_number:'',bi_uuid:''}
            });
          }
          for(var i1=0;i1< this.communityRows.length;i1++){
            let Acomponent  = () => import('@/components/views/baseInfoManager/CustomComponent.vue' ).catch((error) => {
              //console.info("出错了："+error);
              this.$message({
                type: 'error',
                showClose: true,
                duration:0,
                dangerouslyUseHTMLString: true,
                // iconClass:'el-icon-info',
                message: '<spand style="color:black;">出错了：' + error +"</spand>"
              });
            });
            this.editableTabs.push({
              title:  this.communityRows[i1].ci_community_name,
              name:   this.communityRows[i1].ci_uuid,
               show:   this.communityRows[i1].status,
              content: Acomponent
            });
          }


        }else{
          console.info("查询失败");
          loading.close();

          that.communityRows = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.communityRows = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.communityRows = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });




    },
    showOrgInfo(data){
      this.dialogFormVisible1=true;
      console.info("选择换热站："+JSON.stringify(data));
      this.queryCommunity(data);
    },
    showHiddenTree(){

      if(this.showTree){
        // this.icon1 = "el-icon-d-arrow-right";
        this.icon1 = "el-icon-caret-right";
        //this.windowWidth= (this.windowWidth+300);
      }else{
        // this.icon1 ="el-icon-d-arrow-left";
        this.icon1 ="el-icon-caret-left";
        //this.windowWidth = (this.customGlobal.win_w_h.width-300)+'px';
        //this.windowWidth= (this.windowWidth-300) ;
      }
      this.showTree = !this.showTree;

      console.info("是否显示："+this.showTree);


    },
    handleTreeNodeClick(data, node) {
      //console.info('点击事件,可在此处理对应逻辑_data:'+JSON.stringify(data)+",node展开状态:"+  node.expanded );
      //console.log("得到当前选择key_getCurrentKey："+this.$refs.org_tree.getCurrentKey());
      //console.log("得到当前选择key_getCurrentNode："+this.$refs.org_tree.getCurrentNode().caption);
      // console.log("得到当前选择key_getCurrentNode.parent："+this.$refs.org_tree.getNode( this.$refs.org_tree.getCurrentNode()).parent.label);
      let node_label = [];
      let node_key = [];
      node_label.push(this.$refs.org_tree.getCurrentNode().caption);
      node_key.push(this.$refs.org_tree.getCurrentNode().id);
      this.getTreeNodeParentCaption(node_label,node_key,this.$refs.org_tree.getCurrentNode());
      //console.info("node_label:"+node_label.reverse());
      //console.info("node_key:"+node_key.reverse());
      let _level_ = this.$refs.org_tree.getNode(this.$refs.org_tree.getCurrentNode()).level;
      if(_level_==5){
        // this.$message({
        //   type: 'error',
        //   showClose: true,
        //   duration:0,
        //   dangerouslyUseHTMLString: true,
        //   message: "<spand style='color:black;'>不支持对["+this.$refs.org_tree.getCurrentNode().caption+"]层级进行操作</spand>"
        // });
      }else{
        if(this.currentSelTreeNodeId!==this.$refs.org_tree.getCurrentKey()){
          this.currentSelTreeNodeId = this.$refs.org_tree.getCurrentKey();
          for(var i1=0;i1<node_label.length;i1++){
               //node_label[i1] = encodeURIComponent(node_label[i1]);
               node_label[i1] = this.customGlobal.encrypt(node_label[i1],this.customGlobal.cryptoKey);
          }
          this.$router.push({ name:'org_query1',path: '/org_query1', query: { orgid: this.currentSelTreeNodeId ,caption:node_label.reverse(),id:node_key.reverse()}})

        }
      }

    },
    //返回树节点层级caption
    getTreeNodeParentCaption(label,key , node){
      let currentNode = this.$refs.org_tree.getNode(node);
      let len = currentNode.level;
      let loop1 = 1;
     // console.info("level:"+len);
     while(loop1<len){
        if(currentNode.parent!==undefined) {
          label.push(currentNode.parent.label)  ;
          key.push(currentNode.parent.key)  ;
          currentNode = this.$refs.org_tree.getNode(currentNode.parent);
          loop1++;
        }
      }
      return {"label":label,"key":key};


    },
    addTreeNode(currentNodeId,targetNode,layer){

      // this.checkPermissionId('100',this.treeData,{id:'100_3',caption:'上传LOGO'},'1','1');
      this.customGlobal.checkPermissionId(currentNodeId,this.treeData,targetNode,layer,'1');

    },
    delTreeNode(currentNodeId){
      // this.checkPermissionId('100',this.treeData,{id:'100_3',caption:'上传LOGO'},'1','3');
      this.customGlobal.checkPermissionId(currentNodeId,this.treeData,{},'1','3');
      // this.refreshTableData = new Date().getTime();
    },
    modifyTreeNode(currentNodeId,targetNode){
      // this.checkPermissionId('100',this.treeData,{id:'100_3',caption:'上传平台LOGO信息'},'1','2');
      this.customGlobal.checkPermissionId(currentNodeId,this.treeData,targetNode,'1','2');
      // this.refreshTableData = new Date().getTime();
    },
    trigerChildObj(tab, event){
      // console.log(tab, event);
      // console.info("editableTabsValue:"+this.editableTabsValue);

      console.info("点击tab.name:"+tab.name);
      Bus.$emit('globalMsgForCommunityBuilder',{
        community:{ci_uuid:tab.name,ci_community_name:tab.label},
        builder:{bi_build_number:'',bi_uuid:''}
      });




    }


  }
}
</script>

<!--<style scoped>-->
<style >
.el-tree {
  width: 100%!important;
  overflow: auto!important;
}

.el-tree>.el-tree-node {
  display: inline-block!important;
  min-width: 100%!important;
}

.slide-button  {
  background: #f0f0f0;
  height: calc(100vh);
  width: 25px;
  cursor: pointer;
}
.slide-button:hover {
  background: #dbdbdb;

}


.el-tabs__item{
   width:150px;
   text-align: center;
}
.el-tabs__header> .is-left{
  width:150px;
}

.el-tabs__nav-prev, .el-tabs__nav-next{
  right:40%!important;
  left:40%!important;
  width:20px!important;
  height: 20px!important;
}

</style>



